// Frontend calendar block next and previous button css fix
#wptravel-block-trip-calendar {
	.wp-travel-booking__header {
		button {
			@media (max-width: 599px) {
				margin-right: 0;
			}
		}
	}
	.wp-travel-booking__datepicker-wrapper {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		gap: 1rem;
		.react-datepicker-wrapper {
			margin-right: 0;
			.wp-travel-date-picker-btn {
				span {
					padding: 0.75em 1em 0.5em;
				}
			}
			.react-datepicker-popper {
				z-index: 20;
			}
		}
		.react-datepicker {
			.react-datepicker__header {
				select {
					padding: 0 5px;
					height: 35px;
					border-radius: 2px;
				}
			}
		}
		button.react-datepicker__navigation {
			position: absolute;
			border: none;
			&::before {
				font: normal 20px/1 "Font Awesome 5 Free";
				font-weight: 900;
				display: block;
			}
			&.react-datepicker__navigation--previous {
				left: 15px;
				&::before {
					content: "\f053";
				}
			}
			&.react-datepicker__navigation--next {
				left: unset;
				right: 15px;
				margin-right: 0;
				&::before {
					content: "\f054";
				}
			}
		}
	}
	.wp-travel-booking__pricing-wrapper {
		.wp-travel-booking__pricing-name {
			margin-bottom: 1em;
			button { 
				margin-bottom: 1em;
				margin-right: 1em;
			}
		}
	}
	.wp-travel-booking__panel-bottom {
		// position: relative;
		border: 1px solid #eeeeee;
		.left-info,
		.right-info {
			p {
				margin-top: 0;
			}
		}
		.right-info {
			.wp-travel-book {
				margin-right: 0;
			}
		}
	}
}
