// Trip Outline New Layout
.wptravel-block-wrapper {
	&.wptravel-block-trip-outline.layout-2 {
		.itenary {
			h2 {
				text-align: left;
			}

			.col {
				display: block;
				position: relative;
				border-left: 2px solid;
				.tc-heading { 
					width: 100%;
					text-align: left;
				}
				.tc-content {
					border-left: none;
					width: 100%;
					&.left {
						border-right: none;
						@media (max-width: 992px) {
							justify-content: left;
						}
						img {
							float: left;
							margin-left: 0;
							margin-right: 1.5em;
						}
						p {
							text-align: left;
						}
					}
				}
				h3 {
					text-align: left;
				}
			}
			.image {
				top: -110px;
				@media (max-width: 767px) {
					top: 0;
				}
				@media (max-width: 575px) {
					width: 20px;
					height: 20px;
					left: -11px;
				}
			}
		}
	}
}
