.wptravel-block-wrapper {
	&.wptravel-block-trip-filters {
		margin-bottom: 20px;
		.wp-travel-form-field {
			input,
			select {
				width: 100%;
			}
		}
		.wp-travel-search {
			input {
				width: 100%;
			}
		}

		.wp-travel-form-field.wp-trave-price-range {
			.wp-travel-range-slider.ui-slider.ui-slider-horizontal {
				border: none;
				border-radius: 10px;
				background-color: #555;
				cursor: pointer;
				height: 5px;
				position: relative;
				text-align: left;
				.ui-slider-range.ui-corner-all.ui-widget-header {
					position: absolute;
					z-index: 1;
					font-size: 0.7em;
					display: block;
					border: 0;
					background-position: 0 0;
					top: 0;
					height: 100%;
				}
				span.ui-slider-handle.ui-corner-all.ui-state-default {
					border: 1px solid #555555;
					background: #555555;
					font-weight: normal;
					color: #505050;
					cursor: e-resize;
					top: -5px;
					margin-left: -0.6em;
					border-radius: 50%;
					outline: none;
					position: absolute;
					z-index: 2;
					width: 14px;
					height: 14px;
					cursor: pointer;
					touch-action: none;
				}
			}
		}

		.wp-travel-form-field.wp-travel-trip-duration {
			width: 100%;
			padding: 0;
			display: inline-block;
			margin-bottom: 15px;
			vertical-align: top;
			position: relative;
			span.trip-duration-calender {
				width: 50%;
				display: block;
				float: left;
				margin-bottom: 20px;
				position: relative;
				small {
					font-size: 80%;
				}
				input {
					max-width: 95%;
					display: block;
					// padding-left: 35px;
					padding-left: 50px;
				}
				&:last-child {
					float: right;
				}
			}
		}

		@media (max-width: 1199px) {
			padding: 0 20px;
		}
	}
}