.wptravel-block-wrapper {
	&.wptravel-block-trip-search {
		form {
			p {
				input,
				select {
					width: 100%;
				}
			}
		}
		@media (max-width: 1199px) {
			padding: 0 20px;
		}
	}
}

.wptravel-block-wrapper {
	.widget_wp_travel_filter_search_widget {
		.wp-travel-search {
			p {
				margin: 30px 0;
				input {
					width: 100%;
					height: 39px;
					padding: 0.6em 1em 0.6em !important;
				}
				select {
					height: 39px;
					padding: 0.6em 1em 0.6em !important;
				}
			}
		}
	}
}

.wptravel-block-wrapper {
	.widget_wp_travel_filter_search_widget {
		.wp-travel-itinerary-items {
			.wp-travel-form-field {
				margin: 25px 0;
				input {
					height: 39px;
					padding: 0.6em 1em 0.6em !important;
				}
				select {
					height: 39px;
					padding: 0.6em 1em 0.6em !important;
				}
			}
			.wp-travel-trip-duration {
				.trip-duration-calender {
					.calender-icon {
						line-height: 38px !important;
						width: 38px !important;
						position: absolute;
						top: 31px;
						left: 2px;
					}
					input {
						max-width: 200px !important;
						padding-left: 45px !important;
					}
				}
			}
		}
	}
}
