.wptravel-block-wrapper {
	&.wptravel-block-breadcrumb {
		// padding: 20px 0;
		// @media (max-width: 821px) {
		// 	margin: 0;
		// 	margin-left: 0 !important;
		// 	padding-top: 0px;
		// }
		ul {
			display: flex;
			flex-direction: row;
			list-style: none;
			align-items: center;
			padding-left: 0;
			justify-content: flex-start;
			margin: 0;
			gap: 1.5rem;
			@media (max-width: 821px) {
				align-items: unset;
			}
			li {
				position: relative;
				// margin-left: 10px;
				@media (max-width: 821px) {
					display: flex;
				}
				span {
					// margin-left: 10px;
				}

				&::after {
					content: "\00BB";
					position: absolute;
					font-family: wt-icon-solid;
					top: 50%;
					right: -15px;
					transform: translate(0, -50%);
					@media (max-width: 739px) {
						top: 14px;
					}
					// @media (max-width: 650px) {
					// 	right: -14px;
					// }
				}
				&:nth-last-child(1) {
					&::after {
						display: none;
					}
					span {
						// margin-left: 10px;
						text-transform: capitalize;
					}
				}
			}
		}
	}

	a {
		color: inherit;
	}
}
