.wptravel-block-wrapper {
	&.wptravel-block-trip-gallery {
		.wp-travel-advanced-gallery-items-list {
			display: grid;
			grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
			padding: 0;
			column-gap: .5em;
			.item {
				width: 100%;
				img {
					@media (max-width: 507px) {
						height: 200px !important;
					}
				}
			}
		}
	}
}
