.wptravel-block-wrapper {
	&.wptravel-block-trip-rating {
		.wp-travel-average-review {
			width: 6em;
			font-family: "Font Awesome 5 Free";
			font-size: 1em;
			height: 1em;
			line-height: 1;
			overflow: hidden;
			position: relative;
			display: inline-block;
			&:before {
				color: #f9a032;
				content: "\f005\f005\f005\f005\f005";
				float: left;
				left: 0;
				position: absolute;
				top: 0;
			}
			span {
				float: left;
				left: 0;
				overflow: hidden;
				padding-top: 1.5em;
				position: absolute;
				top: 0;
				color: #f9a032;
				font-weight: 900;
			}
		}
	}
}
