.wptravel-block-wrapper {
	&.wptravel-block-trip-faqs {
		.panel-group {
			.wp-collapse-open {
				text-align: right;
			}
			.panel-heading {
				.panel-title {
					margin-bottom: -10px;
				}
				.accordion-toggle {
					border: 1px solid #ddd;
					padding: 10px 40px 10px 15px;
					font-weight: normal;
					display: block;
					.collapse-icon {
						position: relative;
						float: right;
						&::after {
							content: "\f077";
							font-family: "Font Awesome 5 Free";
							font-weight: 900;
							position: absolute;
							right: -20px;
							top: 3px;
						}
					}
					&.collapsed {
						.collapse-icon {
							&::after {
								content: "\f078";
							}
						}
					}
				}
			}
			.panel-collapse {
				.panel-body {
					border: 1px solid #ddd;
					padding: 10px 40px 10px 15px;
					border-top: none;
					p {
						margin-bottom: 0.8em;
						margin-top: 0.8em;
					}
				}
			}
		}
	}
}
