.wptravel-block-wrapper {
	&.wptravel-block-tabs {
		border: 1px solid rgba(222, 222, 222, 49%);
		// margin-bottom: 30px;
		#slider-tab {
			@media (max-width: 768px) {
				display: none;
			}

			ul.tab-list {
				margin: 0;
				border: none;
				@media (min-width: 769px) and (max-width: 992px) {
					height: auto;
				}
				.slick-list.draggable {
					overflow: hidden;
				}
				button {
					position: relative;
					overflow: hidden;
					&.slick-prev,
					&.slick-next {
						&::before {
							font-family: "Font Awesome 5 Free";
							display: block;
							font-weight: 900;
							font-size: 16px;
							position: absolute;
							// top: 0px;
						}
					}
					&.slick-prev {
						&::before {
							content: "\f053";
						}
					}
					&.slick-next {
						&::before {
							content: "\f054";
						}
					}
					&:hover {
						&.slick-prev {
							&::before {
								right: 16px;
							}
						}
					}
				}
				li.wp-travel-ert {
					padding: 20px 26px;
					background: #f7f7f7;
					&.resp-tab-active {
						background: #fff;
					}
				}
			}
		}
		.resp-tabs-container {
			.tab-list-content {
				#booking {
					.wptravel-pax-selector {
						.wp-travel-booking__pax-selector-wrapper {
							ul {
								li {
									.text-right {
										.pricing-area {
											.qty-spinner {
												span {
													// height: 28px;
													height: 30px;
													display: flex;
													justify-content: center;
													align-items: center;
												}
											}
										}
									}
								}
							}
						}
						.wp-travel-booking__trip-extras-wrapper {
							ul {
								li {
									// trip extra "check-circle"
									.checkbox {
										padding: 0;
									}
									.text-right {
										.pricing-area {
											.qty-spinner {
												span {
													height: 28px;
												}
											}
										}
									}
								}
							}
						}
					}
				}
				border: none;
				background: #fff;
				.wp-travel-gallery {
					ul {
						display: flex;
						align-items: center;
						margin: 0;
						@media (max-width: 767px) {
							padding-left: 10px;
						}
						@media (max-width: 575px) {
							display: block;
							padding-left: 0;
						}
						// @media (max-width: 565px) {
						//     flex-wrap: wrap;
						// }
						li {
							list-style: none;
							padding: 0;
							margin-right: 30px;
							@media (max-width: 575px) {
								margin-right: 0;
							}
							a {
								img {
									max-width: 100%;
									height: auto;
									@media (max-width: 575px) {
										width: 100%;
									}
								}
							}
						}
					}
				}
				.wp-travel-advanced-gallery-items-list {
					grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
					// grid-gap: 15px 30px;
					// @media (max-width: 991px) {
					// 	grid-template-columns: repeat(3, 1fr);
					// }
					// @media (max-width: 767px) {
					// 	display: grid;
					// 	grid-template-columns: repeat(2, 1fr);
					// 	grid-gap: 10px 30px;
					// 	padding: 0;
					// }
					// @media (max-width: 575px) {
					// 	display: flex;
					// 	// grid-template-columns: repeat(1, 1fr);
					// 	// grid-gap: 0;
					// }
					.item {
						width: 100%;
						@media (max-width: 767px) {
							width: 100%;
						}
						.wptag__thumbnail {
							img {
								@media (max-width: 767px) {
									height: auto !important;
								}
							}
						}
					}
				}
			}

			#trip_outline {
				.itenary {
					.timeline-contents {
						.tc-content {
							img {
								transform: translateY(8px);
							}
							&.right {
								img {
									float: left;
									margin-right: 1.5em;
									@media (max-width: 767px) {
										float: unset;
										width: 100%;
									}
								}
							}
							&.left {
								img {
									float: right;
									margin-left: 1.5em;
									@media (max-width: 767px) {
										float: unset;
										width: 100%;
									}
								}
							}
						}
					}
				}

				.itenary {
					padding: 20px 0;
				}

				.row-reverse {
					flex-direction: row-reverse;
				}

				.row-reverse .tc-heading {
					text-align: left;
				}

				.col {
					display: flex;
					flex-wrap: nowrap;
				}

				.tc-content {
					position: relative;
					width: calc(50% - 1px);
					padding: 0 50px 15px 50px;
				}

				.tc-content.left {
					border-right: 2px solid;
					text-align: right;
				}

				.tc-content.right {
					border-left: 2px solid;
				}

				.tc-heading .image {
					background: #fff;
					z-index: 111;
				}

				.tc-heading h1,
				.tc-heading h3 {
					/* text-transform: uppercase; */
					margin-bottom: 12px;
				}

				.itenary .btn {
					text-transform: uppercase;
					border: 1px solid #f83531;
					padding: 7px 40px;
					display: inline-block;
					position: relative;
					display: none;
				}

				.tc-heading h4 {
					font-size: 22px;
					text-transform: uppercase;
					margin-top: 0;
					margin-bottom: 0px;
				}

				.tc-heading {
					position: relative;
					width: calc(50% - 1px);
					padding: 0px 50px 15px 50px;
					text-align: right;
				}

				#related-posts .related-post.col {
					min-height: auto;
				}

				.col h3 {
					margin-top: 0;
					font-size: 15px;
					/* text-transform: capitalize; */
					font-weight: bold;
					margin-bottom: 3px;
					flex-basis: 100%;
				}

				.col h3 ~ time.published {
					flex-basis: 100%;
				}

				.itenary .image {
					height: 25px;
					width: 25px;
					// left: -13px;
					left: -15px;
					background: #8b8b8b;
					top: 0;
					border-radius: 50%;
					border: 2px solid;
					position: absolute;
				}

				.itenary .row-reverse .image {
					left: initial;
					// right: -13px;
					right: -15px;
				}

				.timeline-contents .col:last-of-type .tc-content {
					padding-bottom: 0;
				}

				.tc-heading h3:last-of-type {
					font-style: italic;
					font-weight: 100;
				}

				.timeline-contents .col:last-of-type .tc-content p {
					margin-bottom: 0;
				}

				.timeline-contents h2 {
					text-align: center;
				}

				@media screen and (max-width: 767px) {
					.tc-heading.left {
						padding: 0 35px;
					}
					.ite-03 .col {
						position: relative;
						border-left: 2px solid;
						margin-bottom: 0;
						margin-left: 23px;
					}
					.tc-content {
						position: initial;
					}
					.tc-heading.right {
						margin-top: 0px;
						left: 0px;
						padding: 0 35px;
					}
					.tc-content.left {
						border-right: none;
						padding: 0 0 10px 35px;
						text-align: left;
					}
					.itenary .right {
						float: none;
						width: 100%;
					}
					.itenary .left {
						float: none;
						width: 100%;
						text-align: left;
						border-left: none;
					}
					.tc-heading h4 {
						margin-bottom: 0px;
					}
					.col h3 {
						float: none;
						margin-top: 0px;
						margin-right: 0px;
						display: block;
					}
					.itenary .btn {
						padding: 7px 25px;
					}
					.tc-content.right {
						border-left: none;
						margin-top: 0;
						padding: 0 0 10px 35px;
					}
					.itenary .col {
						position: relative;
						border-left: 2px solid;
						margin-bottom: 0;
						margin-left: 11px;
						display: block;
						margin-right: 20px;
					}
					.tc-heading h3:last-of-type {
						margin-bottom: 1em;
					}
					.itenary .row-reverse .image {
						left: -13px;
						right: initial;
					}
				}

				@media screen and (max-width: 575px) {
					.itenary .tc-content {
						border-left: none;
						margin-top: 0;
						padding: 0 0 10px 20px;
					}
					.itenary .image,
					.itenary .row-reverse .image {
						height: 15px;
						width: 15px;
						// left: -8.5px;
						left: -11px;
					}
					.itenary .tc-heading {
						border-left: none;
						margin-top: 0;
						padding: 0 0 10px 20px;
					}
					.itenary .col {
						margin-left: 6px;
					}
					.tc-heading h3:last-of-type {
						margin-bottom: 0;
					}
					.ite-03 .col {
						margin-left: 16px;
					}
				}
			}
		}

		#reviews {
			p.comment-form-comment {
				display: grid;
			}
			.wp-tab-review-inner-wrapper {
				ol.commentlist {
					padding-left: 0;
					@media (max-width: 767px) {
						margin: 5px;
					}
					li {
						list-style: none;
						.comment_container {
							img {
								border-radius: 50%;
							}
							.comment-text {
								.reply {
									a {
										font-weight: bold;
									}
								}
							}
						}
					}
				}
			}
			#review_form_wrapper {
				#review_form {
					.comment-respond {
						margin: 20px 15px 0;
						@media (max-width: 767px) {
							width: 100%;
							margin: 10px 0 0;
						}
					}
				}
			}
		}

		// Booking Dates Tab in frontend
		.wp-travel-list-view {
			.wp-travel-booking__content-wrapper {
				.wptravel-recurring-dates {
					table.wptravel-recurring-table {
						border-collapse: collapse;
						border-spacing: 0;
						margin: 0 0 1.5em;
						width: 100%;
						thead.thead-table {
							tr {
								th {
									background: #f2f3f4;
									font-weight: bold;
									border: none;
									font-size: 14px;
									color: #000;
									line-height: 1.4;
									text-transform: uppercase;
									padding: 10px;
									&:first-child {
										border-top-left-radius: 3px;
										display: flex;
									}
									&:nth-child(2) {
										text-align: left;
									}
									&:last-child {
										border-top-right-radius: 3px;
									}
								}
							}
						}

						tbody {
							background: #fbfafa;
							tr {
								border-bottom: 1px solid #0000000a;
								vertical-align: top;
								position: relative;
								td {
									color: #404040;
									text-align: center;
									border: none;
									padding-top: 15px;
									padding-bottom: 15px;
									padding-left: 10px;
									padding-right: 10px;
									&:not(:nth-child(2)) {
										vertical-align: top;
										text-align: right;
									}
									&:nth-child(1) {
										div {
											margin-bottom: 1em;
											.components-base-control__field {
												display: flex;
												span.components-checkbox-control__input-container {
													.components-checkbox-control__input {
														width: 20px;
														height: 20px;
													}
													.components-checkbox-control__checked {
														display: none;
													}
												}
												label.components-checkbox-control__label {
													font-size: 15px;
													font-weight: bold;
													color: #444;
													margin-left: 0.5em;
													text-align: left;
												}
											}
										}
									}
									&:nth-child(2) {
										@media (min-width: 992px) {
											width: 460px;
										}
										.person-box {
											.components-disabled {
												opacity: 0.5;
											}
											.wp-travel-booking__pax-selector-wrapper {
												margin-bottom: 15px;

												h4 {
													display: none;
												}
												ul.wp-travel-booking__trip-option-list {
													margin: 0;
													padding: 0;
													list-style: none;
													li {
														display: flex;
														flex-wrap: wrap;
														justify-content: space-between;
														position: relative;
														align-items: flex-start;
														.text-left {
															flex: 1;
															margin-right: 0.5em;
															display: flex;
															align-items: center;
															strong {
																font-size: 15px;
																margin-right: 1em;
															}
															span.tooltip.group-discount-button {
																cursor: pointer;
																font-size: 12px;
																display: inline-flex;
																align-items: center;
																text-decoration: underline;
																position: relative;
																transition: all 0.4s ease-in-out;
																> span {
																	display: none;
																}
																svg {
																	width: 15px;
																	height: 15px;
																	margin-right: 0.25em;
																}
																.discount-table {
																	position: absolute;
																	top: 70%;
																	left: 0;
																	z-index: 1;
																	background-color: #fff;
																	border-radius: 4px;
																	padding: 0.5em;
																	width: 250px;
																	margin-top: 10px;
																	box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);
																	border: 1px solid #ccc;
																	opacity: 0;
																	visibility: hidden;
																	animation: arrowAnimate1 1s ease infinite;
																	&::before {
																		content: "";
																		border-bottom: 10px solid #fff;
																		border-left: 10px solid transparent;
																		border-right: 10px solid transparent;
																		position: absolute;
																		left: 10px;
																		transform: translateY(-100%);
																		border-bottom-color: #0000005c;
																		top: -0.7px;
																	}
																	&::after {
																		content: "";
																		border-bottom: 10px solid #fff;
																		border-left: 10px solid transparent;
																		border-right: 10px solid transparent;
																		position: absolute;
																		top: 0;
																		left: 10px;
																		transform: translateY(-100%);
																	}
																	table {
																		width: 100%;
																		border-collapse: collapse;
																		text-align: center;
																		display: table;
																		margin: 0;
																		.discount-thead {
																			th {
																				padding: 0.25em 0.5em;
																				font-size: 14px;
																				text-align: center;
																				color: #404040;
																				border: 1px solid #eee;
																			}
																		}
																		.discount-tbody {
																			tr {
																				td {
																					border: 1px solid #eee;
																					padding: 0.25em 0.5em;
																					font-size: 14px;
																					text-align: center;
																					color: #404040;
																				}
																			}
																		}
																	}
																}
															}
														}
														.text-right {
															display: flex;
															flex-wrap: wrap;
															align-items: center;
															.item-price {
																@media (min-width: 576px) {
																	margin: 0.3em;
																	margin-right: 1em;
																}
																> span {
																	font-size: 16px;
																	font-weight: bold;
																	color: #444;
																}
															}

															.pricing-area {
																.qty-spinner {
																	position: relative;
																	display: flex;
																	align-items: center;
																	button {
																		display: inline-flex;
																		justify-content: center;
																		align-items: center;
																		border-radius: 0;
																		margin: 0;
																		padding: 1px 6px;
																		font-size: 16px;
																		// background: #b3b3b3;
																		// width: 20px;
																		// height: 20px;
																		line-height: 20px;
																		cursor: pointer;
																		&:first-child {
																			border-top-left-radius: 4px;
																			border-bottom-left-radius: 4px;
																		}
																		&:nth-child(3) {
																			border-top-right-radius: 4px;
																			border-bottom-right-radius: 4px;
																		}
																	}
																	span {
																		display: inline-block;
																		min-width: 30px;
																		height: 20px;
																		line-height: 20px;
																		text-align: center;
																		border-top: 1px solid rgba(0, 0, 0, 0.1);
																		border-bottom: 1px solid rgba(0, 0, 0, 0.1);
																		background: none;
																	}
																}
															}
														}
													}
												}
											}
										}
									}
									&:nth-child(3) {
										@media (min-width: 1200px) {
											width: 200px;
										}
										.date-time-wrapper {
											font-weight: 500;
											margin-bottom: 10px;
											.start-date,
											.end-date {
												margin-bottom: 2px;
												display: block;
												span {
													font-weight: 700;
													color: #000;
												}
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}

