		.wp-travel-booking__header {
			button {
				display: flex;
				align-items: center;
				background: transparent;
				border: 1px solid #df1814;
				color: #df1814;
				float: right;
				margin-bottom: 15px;
				width: auto;
				padding: 8px;
				svg {
					width: 16px;
					fill: #df1814;
					margin-right: 6px;
				}
			}
		}

		.wp-travel-booking__pricing-wrapper.wptravel-pax-selector {
			.wp-travel-booking__pax-selector-wrapper {
				// margin-bottom: 15px;
				margin-bottom: 0;
				h4 {
					display: none;
				}
				ul.wp-travel-booking__trip-option-list {
					margin: 0;
					padding: 0;
					list-style: none;
					li {
						display: flex;
						flex-wrap: wrap;
						justify-content: space-between;
						position: relative;
						align-items: center;
						.text-left {
							flex: 1;
							margin-right: 0.5em;
							display: flex;
							align-items: center;
							strong {
								font-size: 15px;
								margin-right: 1em;
							}
							span.tooltip.group-discount-button {
								cursor: pointer;
								font-size: 12px;
								display: inline-flex;
								align-items: center;
								text-decoration: underline;
								position: relative;
								transition: all 0.4s ease-in-out;
								> span {
									display: none;
								}
								svg {
									width: 15px;
									height: 15px;
									margin-right: 0.25em;
								}
								.discount-table {
									position: absolute;
									top: 70%;
									left: 0;
									z-index: 1;
									background-color: #fff;
									border-radius: 4px;
									padding: 0.5em;
									width: 250px;
									margin-top: 10px;
									box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);
									border: 1px solid #ccc;
									opacity: 0;
									visibility: hidden;
									animation: arrowAnimate1 1s ease infinite;
									&::before {
										content: "";
										border-bottom: 10px solid #fff;
										border-left: 10px solid transparent;
										border-right: 10px solid transparent;
										position: absolute;
										left: 10px;
										transform: translateY(-100%);
										border-bottom-color: #0000005c;
										top: -0.7px;
									}
									&::after {
										content: "";
										border-bottom: 10px solid #fff;
										border-left: 10px solid transparent;
										border-right: 10px solid transparent;
										position: absolute;
										top: 0;
										left: 10px;
										transform: translateY(-100%);
									}
									table {
										width: 100%;
										border-collapse: collapse;
										text-align: center;
										display: table;
										margin: 0;
										.discount-thead {
											th {
												padding: 0.25em 0.5em;
												font-size: 14px;
												text-align: center;
												color: #404040;
												border: 1px solid #eee;
											}
										}
										.discount-tbody {
											tr {
												td {
													border: 1px solid #eee;
													padding: 0.25em 0.5em;
													font-size: 14px;
													text-align: center;
													color: #404040;
												}
											}
										}
									}
								}
							}
						}
						.text-right {
							display: flex;
							flex-wrap: wrap;
							align-items: center;
							.item-price {
								@media (min-width: 576px) {
									margin: 0.3em;
									margin-right: 1em;
								}
								> span {
									font-size: 16px;
									font-weight: bold;
									color: #444;
								}
							}

							.pricing-area {
								.qty-spinner {
									position: relative;
									display: flex;
									align-items: center;
									button {
										display: inline-flex;
										justify-content: center;
										align-items: center;
										border-radius: 0;
										margin: 0;
										padding: 0 !important;
										font-size: 16px;
										// background: #b3b3b3;
										// width: 20px;
										// height: 20px;
										line-height: 20px;
										cursor: pointer;
										&:first-child {
											border-top-left-radius: 4px;
											border-bottom-left-radius: 4px;
										}
										&:nth-child(3) {
											border-top-right-radius: 4px;
											border-bottom-right-radius: 4px;
										}
									}
									span {
										display: inline-block;
										min-width: 30px;
										height: 20px;
										line-height: 20px;
										text-align: center;
										border-top: 1px solid rgba(0, 0, 0, 0.1);
										border-bottom: 1px solid rgba(0, 0, 0, 0.1);
										background: none;
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

