.wptravel-block-wrapper {
	#wp-travel-enquiries {
		padding: 0;
		background: transparent;
		max-width: 100%;
	}

	&.wptravel-block-trip-enquiry {
		form {
			textarea {
				width: 100%;
			}
			.wp-travel-form-field {
				input:not([type="radio"]):not([type="checkbox"]):not([type="submit"]) {
					width: 100% !important;
				}
				.parsley-errors-list {
					list-style: none;
					// right: 0;
					top: 5px;
					li {
						color: red;
					}
				}
			}
		}
		@media (max-width: 1199px) {
			padding: 0 20px;
		}
	}
}
