.wp-travel-itinerary-items {
	ul {
		display: grid;
		padding: 0;
		grid-template-columns: repeat(3, 1fr);
		// grid-template-columns: repeat(auto-fill, minmax(225px, 1fr));
		grid-gap: 0;
		margin-left: 0;
		@media (max-width: 1199px) {
			display: grid !important;
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 25px;
			padding: 0 15px;
		}
		@media (max-width: 575px) {
			grid-template-columns: repeat(1, 1fr);
		}
		li {
			list-style: none;
			border: 1px solid #e0e0e0;
			// margin-right: 25px;
			// padding-bottom: 25px;
			position: relative;
			@media (max-width: 1199px) {
				width: auto !important;
			}

			.wp-travel-post-item-wrapper {
				.wp-travel-post-wrap-bg {
					.wp-travel-post-thumbnail {
						img.wp-post-image {
							width: 100%;
							max-width: 100%;
							height: auto;
						}
					}
					.wp-travel-savings {
						position: absolute;
						background: #00921d;
						color: #fff;
						padding: 5px 8px;
						text-align: center;
						z-index: 2;
						right: -8px;
						top: 20px;
						&::after {
							content: "";
							position: absolute;
							right: 0px;
							top: 100%;
							z-index: 1;
							border-left: 4px solid #005d12;
							border-right: 4px solid transparent;
							border-bottom: 2px solid transparent;
							border-top: 3px solid #005d12;
						}
					}
					.wp-travel-offer {
						position: absolute;
						left: -6px;
						top: 10px;
						z-index: 1;
						overflow: hidden;
						width: 75px;
						height: 64px;
						span {
							font-size: 12px;
							font-weight: 600;
							color: #fff;
							text-transform: uppercase;
							text-align: center;
							line-height: 34px;
							width: 75px;
							display: block;
							background: #f83531;
							position: absolute;
							top: 10px;
							left: 0;
							&::before {
								content: "";
								position: absolute;
								left: 0px;
								top: 100%;
								z-index: -1;
								border-left: 3px solid transparent;
								border-right: 3px solid #ad2320;
								border-bottom: 3px solid transparent;
								border-top: 3px solid #ad2320;
							}
						}
					}
				}
				.wp-travel-post-info {
					display: flex;
					justify-content: space-between;
					// align-items: center;
					// flex-wrap: wrap;
					flex-wrap: nowrap;
					align-items: unset;

					h4.post-title {
						padding: 0 15px 0 0;
					}
					.wp-travel-trip-detail {
						// padding: 0 15px;
						.trip-price {
							span.person-count {
								display: block;
								text-align: right;
							}
						}
					}
				}

				.wp-travel-post-content {
					padding: 0 15px;
					span.post-category {
						.entry-meta {
							// display: flex;
							// justify-content: space-between;
							// align-items: center;
							// flex-wrap: wrap;
							.wp-travel-average-review {
								font-family: "Font Awesome 5 Free";
								font-size: 1em;
								height: 1em;
								line-height: 1;
								overflow: hidden;
								position: relative;
								width: 5.5em;
								display: inline-block;
								&::before {
									// color: #f9a032;
									content: "\f005\f005\f005\f005\f005";
									float: left;
									left: 0;
									position: absolute;
									top: 0;
								}
								> span {
									// display: none;
								}
							}
							.wp-travel-review-text {
								margin-top: 7px;
							}
						}
					}
				}
			}
		}
	}
}
